* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.container .heading {
  font-size: 3rem;
  text-align: center;
}
.container .card {
  max-width: 35rem;
  border: 2px solid white;
  box-shadow: 0 2px 5px 2px rgba(0, 0, 0, 0.2);
  padding: 2rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.container .card .card__img {
  height: 15rem;
  width: 100%;
}
.container .card .card__title {
  font-size: 1.8rem;
  font-weight: 600;
}
.container .card .card__desc {
  font-size: 1.4rem;
}
.container .card .card__desc::first-letter {
  font-size: 2rem;
  color: crimson;
  font-weight: 700;
}